
header{
   	   /*width: 100%;*/
   	   height:2.2rem;
   	   background: #D43D3D;
   	   display: -webkit-box;
   	   display: -ms-flexbox;
   	   display: flex;
   	   -webkit-box-pack: justify;
   	       -ms-flex-pack: justify;
   	           justify-content: space-between;
   	   text-align: center;
   	   -webkit-box-align: center;
   	       -ms-flex-align: center;
   	           align-items: center;
   	   padding-left:0.65rem;
   	   padding-right:0.55rem;
}
header>.center{
   	    color: white;
   	    font-size:1rem;
}
header>.left{
   	    width: 1.1rem;
   	    height: 1rem;
}
header>.left  img{
   	width: 100%;
   	height: 100%;
}
header>.right{
   	    width: 0.9rem;
   	    height: 0.95rem;
}
header>.right  img{
   	width: 100%;
   	height: 100%;
}
   



