
*{
	   	  padding: 0;
	   	  margin: 0;
	   	  list-style: none;
	   	  text-decoration: none;
}
html{
	   
	   	height: 100%;
}
body{
	   	width: 100%;
	   	height: 100%;
	   	display: -webkit-box;
	   	display: -ms-flexbox;
	   	display: flex;
	   	-webkit-box-pack: justify;
	   	    -ms-flex-pack: justify;
	   	        justify-content: space-between;
	   	-webkit-box-orient: vertical;
	   	-webkit-box-direction: normal;
	   	    -ms-flex-direction: column;
	   	        flex-direction: column;
	   	position: relative;
}
.content{
	   	    -webkit-box-flex: 1;
	   	        -ms-flex-positive: 1;
	   	            flex-grow: 1;
	   	    background: #F4F5F6;
	  	    overflow: hidden;
}
.content>.bar  ul{
	   	 width: 100%;
	   	height: 1.8rem;
	 
	   	     display: -webkit-box;
	 
	   	     display: -ms-flexbox;
	 
	   	     display: flex;
	   	     -ms-flex-pack: distribute;
	   	         justify-content: space-around;
	   	     -webkit-box-align: center;
	   	         -ms-flex-align: center;
	   	             align-items: center;
}
.content>.bar  ul li{
	   	     font-size: 0.75rem;
}
.content>.bar ul li  .li1{
	   	        color: red;
}
.content>.bar ul  .li2{
	   	       width: 0.85rem;
	   	       height: 0.85rem;
}
.content>.bar ul  .li2 img{
	   	       width: 100%;
	   	       height:100%;
} 



